/* PAGE */
body {
  height: 100vh;
  width: 100vw;
  text-align: center;
  position: fixed;
  overflow-y: scroll;
}
.app {
  width: 100%;
  min-width: 800px;
  height: 100%;
  position: fixed;
  min-height: 900px;
}
.wrapper {
  width: 100%;
  height: 100%;
  /* background-color: #faf2f2; */
  z-index: -1;
}
.offset {
  position: relative;
  top: -180px;
}

/* BACKGROUND */
.background {
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  margin: 0;
  position: fixed;
  /* opacity: 0.8; */
  /* z-index: -1; */
}
.gaze {
  /* should height be 100% here for bigger monitors? */
  height: 1120px;
  background-image: url('images/gaze.jpeg') ;
}
.hands {
  height: 100%;
  background-image: url('images/hands.jpeg') ;
}

/* CONTAINERS */
.container {
  height: 100%;
  width: 100%;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
}
.flexrow {
  display: flex;
  justify-content: center;
  align-items: center;
}
.flexcol {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.box {
  height: 400px;
  height: 50%;
  width: 100%;
  /* margin-top: 200px; */
  margin: 200px 30px;
  padding: 30px;
  background-color: white;
  opacity: 0.85;
  border: 1px #171616 solid;
  justify-content: center;
  border: 3px #171616 solid;
  z-index: 9999;
}



/* NAVBAR */
.nav-menu { display: none; }

.navbar {
  /* width: 100%; */
  z-index: 1;
  height: 170px;
  padding: 10px 40px 0 40px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.navbar h2 {
  font: 18pt 'Spline Sans Mono', monospace;
  padding-top: 6px;
}

.navbar p {
  font: 22pt'Spline Sans Mono', sans-serif;
}


#lily {
  font: 60pt 'Princess Sofia', serif;
  position: relative;
  top: 10px;
}

#lily2 {
  font-size: 50pt;
  position: relative;
  top: 18px;
  left: -1px;
}

#and {
  z-index: 0;
  font: 60pt 'Princess Sofia', serif;
  position: relative;
  top: 10px;
  left: 5px;
}

#dan {
  font: 60pt 'Princess Sofia', serif;
  position: relative;
  top: 9px;
  left: 12px;
}

#dan2 {
  font-size: 50pt;
  position: relative;
  top: 17px;
  left: -5px;
}

#line { margin-top: 13px; }

.link-list {
  display: flex;
  justify-content: space-between;
  list-style-type: none;
}

.link-list li {
  padding-left: 60px;
}

.link-list a {
  font: 16pt 'Zilla Slab', serif;
  text-decoration: none;
  color: #171616;
}

.link-list a:hover {
  color: lightblue;
}

.link-list a.active {
  position: relative;
}

.link-list a.active::after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #171616;
}




/* TEXT */
h1, h2, hr, p {
  margin: 0;
  color: #171616;
}
.box h1 {
  font: normal 54pt 'Cormorant Infant', serif;
}

/* LOGIN */
#password-form {
  width: 100%;
  margin-top: 35px;
}
#password {
  padding: 2px 10px;
  width: 50%;
  height: 42px;
  font: 11pt 'Montserrat', sans-serif;
  border-radius: 25px;
}
#password-button {
  margin-left: 5px;
  height: 50px;
  width: 50px;
  font-size: 32pt;
  background-color: white;
  border: none;
}
#password-button:hover {
  color: #c4d9ef;
}
#message {
  color: red;
}



