/* HOME */
#rsvp {
  padding: 10px 40px;
  font: bold 26pt 'Montserrat', sans-serif;
  color: #171616;
  border: 4px #171616 solid;
  background-color: #faf2f2;
  text-decoration: none;
}
#rsvp:hover {
  color: lightblue;
  border-color: lightblue;
}








/* MOBILE */
@media (max-width: 768px) {
  .nav-elements ul li:not(:last-child) {
    margin-right: 30px;
  }
}

@media (max-width: 600px) {
  .nav-menu {
    display: block;
    cursor: pointer;
    color: #ad1a64;
  }

  .nav-elements {
    position: absolute;
    right: 0;
    top: 60px;
    background-color: #e1ebe7;
    width: 0px;
    height: calc(100vh - 60px);
    transition: all 0.3s ease-in;
    overflow: hidden;
    font-size: 12pt;
  }

  .nav-elements.active {
    width: 250px;
  }

  .nav-elements ul {
    display: flex;
    flex-direction: column;
  }

  .nav-elements.active ul li {
    margin-right: 0;
    margin-top: 22px;
  }

  .page-wrapper {
    padding: 30px;
  }

  .rsvp-button {
    width: 100px;
    margin-top: 40px;
    border: 2px #171616 solid;
    font-size: 16pt;
  }
}
